//이런식으로 가독성이 좋아짐.
div.container {
  h4 {
    color : purple;
  }
  p {
    color : $메인칼라 //변수로 원하는 곳에 사용
  }
}

.my-alert {
  background-color: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  margin: auto;
  p {
    margin-bottom: 0;
  }
}

.my-alert-brown {
  //my-alert에 있는 css속성을 모두 가져옴
  //클래스명만 바꿔주면 쉽게 CSS속성 변경 가능, 부트스트랩과 똑같음
  @extend .my-alert;
  background-color: brown
}

@mixin 함수(){
  background-color: #000;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  margin: auto;
  p {
    margin-bottom: 0;
  }
}

.my-alert-mixin {
  @include 함수()
}